<script>
  const getCountries = async () => {
    const response = await fetch('https://admin.feriayecla.com/gestioninterna/funciones/paises.php');
    return await response.json();
  }
</script>
<label for="nacionalidad" class="required">Nacionalidad</label>
<select id="nacionalidad" type="text" name="nacionalidad">
  {#await getCountries() then countries}
  {#each Object.keys(countries) as code}
  <option value="{code}">{countries[code]}</option>
  {/each}
  {/await}
</select>

<label for="nif" class="required">NIF</label>
<input id="nif" type="text" name="nif"/>